import React, { Component } from 'react'

import AppHeader from '../app-header/';
import SearchPanel from '../search-panel/';
import PostStatusFilter from '../post-status-filter/';
import PostList from '../post-list/';
import PostAddForm from '../post-add-form/'

import style from './App.module.css'

export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data : [
        { label: 'Клікнувши на мене можна лайкнути', important: false, like: false, id: 1  },
        { label: 'Якщо на зірочку то важливий', important: false, like: false, id: 2  },
        { label: 'Якщо на кошик то я видалюсь', important: false, like: false, id: 3  },
        { label: 'Мене можна знайти в полі пошук по содержимому текста', important: false, like: false, id: 4  },
        { label: 'Можна переглянути всі які сподобались', important: false, like: false, id: 5  },
      ],
      term: '',
      filter: 'all'
    }
    
    this.deleteItem = this.deleteItem.bind(this);
    this.addItem = this.addItem.bind(this);
    this.onToggleImportant = this.onToggleImportant.bind(this);
    this.onToggleLiked = this.onToggleLiked.bind(this);
    this.onUpdateSearch = this.onUpdateSearch.bind(this);
    this.onFilterSelect = this.onFilterSelect.bind(this);

    this.maxId = 4;
  }

  deleteItem(id) {
    this.setState(({data}) => {
      const index = data.findIndex(elem => elem.id === id);
      
      const newData = [...data.slice(0, index), ...data.slice(index + 1)];

      return {
        data: newData
      }
    })
  }

  addItem(body) {
    const oldItem = this.state.data,
          newItem = { ...oldItem, label: body, id: this.maxId++ };

    this.setState(({data}) => {
      const newArr = [...data, newItem];
      return {
        data : newArr
      }
    });
  }

  onToggleImportant(id) {
    this.setState(({data}) => {
      const index = data.findIndex(item => item.id === id);

      const old = data[index];

      const newItem = { ...old, important: !old.important };

      const newArr = [...data.slice(0, index), newItem, ...data.slice(index + 1)];

      return {
        data : newArr
      }

    });
  }

  onToggleLiked(id) {
    this.setState(({data}) => {
      const index = data.findIndex(elem => elem.id === id);

      const old = data[index];

      const newItem = { ...old, like: !old.like };

      const newArr = [...data.slice(0, index), newItem, ...data.slice(index + 1)];

      return {
        data : newArr
      }
    })
  }

  searchPost(items, term) {
    if (term.length === 0) {
      return items;
    }
    return items.filter( (item) => {
      return item.label.indexOf(term) > -1
    });
  }

  onUpdateSearch(term) {
    this.setState({term})
  }

  filterPost(items, filter) {
    if (filter === 'like') {
      return items.filter(item => item.like);
    } else {
      return items;
    }
  }

  onFilterSelect(filter) {
    this.setState({filter});
  }

  render() {
    const { data, term, filter } = this.state;
    
    const liked = data.filter(item => item.like).length,
          allPost = data.length;
    
    const visiblePost = this.filterPost(this.searchPost(data, term), filter); 

    return (
      <div className={style.app}>
        <AppHeader
          liked={liked}
          allPost={allPost}
        />
        <div className="search-panel d-flex">
          <SearchPanel 
            onUpdateSearch={this.onUpdateSearch} 
          />
          <PostStatusFilter
            filter={filter}
            onFilterSelect={this.onFilterSelect}
          />
        </div>
        <PostList 
          posts={visiblePost}
          onDelete={this.deleteItem}
          onToggleImportant={this.onToggleImportant}
          onToggleLiked={this.onToggleLiked}
          />
        <PostAddForm onAdd={this.addItem}/>
      </div>
    )
  }
}